import React, { useState, useEffect } from 'react';
import './styles/QuestPost.scss';
import { useForm, Controller } from 'react-hook-form';
import Select from 'react-select';
import axios from 'axios';

function QuestPost() {
  const [quest, setQuest] = useState([]);
  const { control, register, handleSubmit, reset: resetForm } = useForm();
  const apiKey = '76fqibqibiqefb'; // changed apiKey and made it permanent for deployment

  const onSubmit = (data) => {
    axios
      .post(
        `https://kaamelot-server.herokuapp.com/quests?apiKey=${apiKey}`,
        data
      )
      .then((res) => res.data)
      .then((newQuest) => {
        setQuest((oldList) => [...oldList, newQuest]);
        resetForm();
      })
      .catch(console.error);
  };

  useEffect(() => {
    axios
      .get('https://kaamelot-server.herokuapp.com/quests')
      .then((res) => res.data)
      .then(setQuest);
  }, []);

  return (
    <div className='post'>
      <h1 className='title'>Add Quest</h1>
      <br />
      <form className='form' onSubmit={handleSubmit(onSubmit)}>
        <div className='questTitle'>
          <label htmlFor='name'></label>
          <input
            name='name'
            required
            placeholder="Quest's Title"
            ref={register}
          />
        </div>
        <br />
        <div className='location'>
          <label htmlFor='localization'></label>
          <input
            name='localization'
            required
            placeholder='Location'
            ref={register}
          />
        </div>
        <br />

        <div className='assignment'>
          <Controller
            name='assignment'
            as={Select}
            options={[
              { label: 'Bohort' },
              { label: 'Calogrenant' },
              { label: 'Galessin' },
              { label: 'Gauvain' },
              { label: 'Karadoc' },
              { label: 'Lancelot' },
              { label: 'Leodagan' },
              { label: 'Perceval' },
              { label: 'Yvain' },
            ]}
            control={control}
            rules={{ required: true }}
          />
        </div>
        <br />
        <div className='value'>
          <br />
          <Controller
            name='value'
            as={Select}
            options={[
              { label: 'Easy', value: '1' },
              { label: 'Medium', value: '2' },
              { label: 'Hard', value: '3' },
              { label: 'Warrior', value: '4' },
              { label: "You're gonna die !", value: '5' },
            ]}
            control={control}
            rules={{ required: true }}
          />
        </div>
        <br />
        <div className='reward'>
          <label htmlFor='reward'></label>
          <textarea
            name='reward'
            required
            placeholder='Reward'
            ref={register}
          ></textarea>
        </div>
        <br />
        <div className='questDetails'>
          <label htmlFor='questDetails'></label>
          <textarea
            name='questDetails'
            required
            placeholder="Quest's Description"
            ref={register}
          ></textarea>
        </div>
        <br />
        <input type='submit' className='add' placeholder='add' />
      </form>
    </div>
  );
}

export default QuestPost;
